  border-radius: 5px;
  justify-content: space-between;
  font-family: 'OpenSans-SemiBold';
  font-weight: 700;
}

.step {
  position: relative;
  display: flex;
  flex-direction: column;
  flex-grow: 1;
  flex-basis: 0;
  align-items: center;
  justify-content: center;
}

.step__marker {
  width: 35px;
  height: 35px;
  display: flex;
  align-items: center;
  border-radius: 50%;
  font-weight: 700;
  justify-content: center;
  background: #b5b5b5;
  color: #fff;
  border: 4px solid #fff;
  z-index: 1;
}

.steps__line {
  position: absolute;
  border: 2px solid #b5b5b5;
  width: 70%;
  top: 4.5%;
  left: 15%;
}

.step.is-active .step__marker {
  background: #19ddfe;
  border: 4px solid #f26120;
}

.step.is-completed .step__marker {
  background: #f26120;
  border: 4px solid #f26120;
}

.error {
  border: 1px solid red;
}

.step:not(:first-child)::before {
  height: .2em;
  width: 100%;
  bottom: 0;
  left: -50%;
  top: 1rem;
  content: " ";
  display: block;
  position: absolute;
  background: #b5b5b5;
}

.step.is-active:not(:first-child)::before, .step.is-completed:not(:first-child)::before {
  background: #f26120;
}
